.emailList {
    flex: 1;
    overflow: scroll;
}

.emailList__settings {
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    border-bottom: 1px solid whitesmoke;
    background-color: white;
    z-index: 100;
    padding-right: 10px;
}

.emailList__sections {
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    border-bottom: 1px solid whitesmoke;
    z-index: 100;
}

.emailList__list {
    padding-bottom: 20%;
}

/* Hides scrollbar for chrome, safari, and opera */
.emailList::-webkit-scrollbar {
    display:none
}